import React, { useState, useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import axios from 'axios';

import './NavSide.scss';

const NavSide = () => {
	const [pages, setPages] = useState(null);

	useEffect(() => {
		axios
			.get('http://sidorchik.ru/reshuege/api/sidebar/menu/')
			.then(response => response.data)
			.then(data =>
				data.map(({ url, exact, text, hint }) => ({
					url,
					exact,
					text,
					hint
				}))
			)
			.then(data => setPages(data))
			.catch(error => {
				console.log(error);

				const data = [
					{
						url: '/manual',
						exact: false,
						text: ' s',
						hint: ', ,  '
					},
					{
						url: '/prob_catalog',
						exact: false,
						text: ' ',
						hint:
							' ,      '
					},
					{
						url: '/',
						exact: true,
						text: '',
						hint: ' ,  '
					},
					{
						url: '/teacher',
						exact: false,
						text: '',
						hint:
							'     ,  '
					},
					{
						url: '/methodist',
						exact: false,
						text: '',
						hint:
							'  ,     '
					},
					{
						url: '/expert',
						exact: false,
						text: '',
						hint: '  ,    '
					},
					{
						url: '/course',
						exact: false,
						text: '',
						hint:
							'  ,    '
					},
					{
						url: '/handbook',
						exact: false,
						text: '',
						hint: '   '
					},
					{
						url: '/page/theory',
						exact: false,
						text: '',
						hint: '  '
					},
					{
						url: '/guestbook',
						exact: false,
						text: ' ',
						hint: '   '
					},
					{
						url: '/callback?f=faq',
						exact: false,
						text: '  ',
						hint: '       '
					},
					{
						url: '/user_stat',
						exact: false,
						text: ' ',
						hint: ',   '
					},
					{
						url: '/favorite',
						exact: false,
						text: '',
						hint: '   '
					}
				];

				setPages(data);
			});
	}, []);

	return (
		<div className="NavSide">
			{pages &&
				pages.map(({ url, exact, text, hint }, i) => (
					<NavLink
						className="Link Link_black Link_wrap NavSide-Link"
						activeClassName="NavLink_selected Link_selected"
						to={url}
						exact={exact}
						title={hint}
						key={i}
					>
						<u className="Link-U Link_black-U Link_wrap-U">
							{text}
						</u>
					</NavLink>
				))}
		</div>
	);
};

export default NavSide;
